@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;700;800&display=swap);

/* COLOURS */
/* 
ADULLAM BLUE            #32A1D5,
FOOTER & ACTIVE BLACK   #1E1E1E
GRAY                    #C4C4C4
LIGHT GRAY              #EEEEEE
*/

/* FONTS */

:root {
    /* color variables */
    --adullam-blue: #32A1D5;
    --footer-black: #1E1E1E;
    --gray-colour: #c4c4c4;
    --lightgray: #EEEEEE;
}

* {
    margin: 0;
    padding: 0;
    font-family:'Montserrat', sans-serif;
    box-sizing: border-box;

}
html {
    width: 100vw;
    height: 100vh;
}

/* GENERAL STYLES */

.container {
    width: 75%;
    margin: 1.2rem auto;
    padding: 1rem;
    display: flex;
    justify-content: center;
}
.section-header h1 {
    font-size: 2.3rem;
    margin:0;
    padding: 1rem;
    text-align: center;
}

p {
    line-height: 1.3;
    margin-bottom: 1rem;
    font-size: 1.1rem;
}
h2 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: center;
}
h3 {
    margin: 1rem auto;
    font-size: 1.2rem;
}


/* OVERLAY */
.overlay {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.5);
}

/* Main Navigation  */

.main-header {
  height: 3.75rem;
  width: 100%;
  background: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
}

.main-header .logo-container {
  width: 6.25rem;
}
.logo {
  width: 100%;
  height: auto;
}
.nav-links {
  list-style-type: none;
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  font-weight: bold;
  
}
.nav-links li {
  margin: 1rem;
  text-align: center;
}
.nav-link {
  text-decoration: none;
  color: #1E1E1E;
  color: var(--footer-black);
}
.nav-link:hover {
  background: #1E1E1E;
  background: var(--footer-black);
  color: #fff;
}

/* Mobile Menu Icon */
.mobile-menu {
  position: fixed;
  background: transparent;
  width: 2.5rem;
  height: 2.5rem;
  margin: 1rem;
  cursor: pointer;
  border: none;
  display: none;
  z-index: 3;
  top: 0;
  left: 0;
  transition: all .2s;
}
.menu-bar {
  width: 70%;
  height: 0.2rem;
  border-radius: 0.1rem;
  background: #000;
  display: flex;
  margin: 0.3rem auto;
}
.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  background: white;
  padding: 1rem;
  width: 55%;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  display: none;
}
.mobile-nav .footer-logo {
  margin: 2rem auto;
}
.mobile-links li {
  list-style: none;
  margin-top: 1rem;
  font-size: 1rem;
  font-weight: bold;
}
.mobile-links li:hover {
  color: #32A1D5;
  color: var(--adullam-blue);
}

/* slider */
.slider {
  position: relative;
  width: 100vw;
  height: 34.375rem;
  /* height: 50vh; */
  /* display: flex; */

}

.myslider {
  height: 100%;
  width: 100%;
  overflow: hidden;   
  display: flex;
}
.second-slide {
  display: block;
}
.slider .slider-img{
  width: 100%;
  height:100%;
  object-fit: cover;
}
.prev, .next {
  position: absolute;
  align-items: center;
  justify-content: center;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  font-size: 60px;
  font-weight: 700;
  padding: 15px;
  cursor: pointer;
  color: #fff;
  transition: 0.1s;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
} 
.prev:hover, .next:hover {
  color: #3498bd;
}
.next {
  right: 0;
}
.dotsbox {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  bottom: 20px;
  text-align: center;
  cursor: pointer;
}
.dot {
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 3px solid #fff;
  border-radius: 50%;
  margin: 0 10px;
  cursor: pointer;
}
.active, .dot:hover {
  border-color: #32A1D5;
}
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: 0.8}
  to {opacity: 1}
}
@keyframes fade {
  from {opacity: 0.8}
  to {opacity: 1}
}
.txt {
  position: absolute;
  color: #fff;
  line-height: 2rem;
  top: 40%;
  left: 15%;
  -webkit-animation-name: posi;
  -webkit-animation-duration: 2s;
  animation-name: posi;
  animation-duration: 2s;
  z-index: 1;
}

@-webkit-keyframes posi {
  from {left: 25%;}
  to {left: 15%;}
}
@keyframes posi {
  from {left: 25%;}
  to {left: 15%;}
}

.txt h1 {
  color: #fff;
  font-weight: bold;
  margin-bottom: 0.7rem;
  font-size: 3rem;
}
.txt p {
  font-weight: bold;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.apply-btn {
  text-decoration: none;
  background: #32A1D5;
  background: var(--adullam-blue);
  color: #fff;
  font-weight: bold;
  border-radius:0.5rem ;
  padding: 0.8rem;   
}

.slider-img {
  /* transform: scale(1.5, 1.5);
  -webkit-animation-name: zoomin;
  -webkit-animation-duration: 40s;
   animation-name: zoomin;
   animation-duration: 40s; */
}
@-webkit-keyframes zoomin {
  from { -webkit-transform: scale(1, 1); transform: scale(1, 1);}
  to {-webkit-transform: scale(1.5, 1.5);transform: scale(1.5, 1.5);}
}
@keyframes zoomin {
  from { -webkit-transform: scale(1, 1); transform: scale(1, 1);}
  to {-webkit-transform: scale(1.5, 1.5);transform: scale(1.5, 1.5);}
}


/* ABOUT SECTION */

#about {
  background-color: #32A1D5;
  background-color: var(--adullam-blue);
  padding: 3.5rem 0;
}
.about-content {   
  padding: 1rem;
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}
.about-text {
  text-align: justify;
  padding: 1.2rem;
  display: flex;
  flex-direction: column;
}

.about-text ul {
  margin-bottom: 1rem;
  display: flex;
}
.about-text li {
  margin: 2rem;
}
.readmore-btn {
  display: block;
  text-decoration: none;
  background: #000;
  padding: 0.6rem;
  margin: 0.7rem auto;
  border-radius: 0.7rem;
  font-size: 0.8rem;
  font-weight: bold;
  color: #fff;
  text-align: center;
  width: 25%;
  align-self: center;
}
.readmore-btn:hover {
  background: #fff;
  color: #32A1D5;
  color: var(--adullam-blue);
}

/* Youtube section */

.youtube {
  display: flex;
  vertical-align: center;
  flex-direction: column;
  max-width: 90%;
}

.youtube .about-video{
  margin: 1rem auto;
  width: 25rem;
  height: 18.75rem;

}

/* TESTIMONIES SECTION */

#testimonies {
  padding: 3.5rem 0;
  background-image: linear-gradient(to right bottom, rgba(0,0,0,.6), rgba(0,0,0,.6)),url(/static/media/slider-image.aaf8a438.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
}

.testimony-card {
  text-align: center;
  margin: 0 0.5rem;
  padding: 1rem;
  background-color: #fff;
  border-radius: 0.8rem;
  max-width: 18rem;
  min-width: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.testifier-img-container{
  width: 100px;
  height: 100px;
  background-color: white;
  border-radius: 50%;
  margin: 0.5rem auto;
}
.testifier-img-container img {
  width: 100%;
  height: auto;
  border-radius: inherit;
}

/* WHAT YOU WILL LEARN */

#what-to-learn{
  background: #EEEEEE;
  background: var(--lightgray);
  padding: 3.5rem 0;
}
.courses {
  flex-direction: column;
  padding: 0.6rem;
}
.courses h3 {
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
}
.course-cards h4 {
  margin: 1.5rem 0;
}
.course-cards {
  display: flex;
  justify-content: center;
  padding: 1rem;
}
.course-card h4 {
  text-align: center;
  font-size: 1.3rem;
  font-weight: bold;
}
.course-card {
  margin: 0.5rem 2rem;
  padding: 1.5rem 3rem;
  background-color: #ccc;
  max-width: 30rem;
}
.course-card ol {
  margin: 1rem;
}
.course-card li {
  margin-top: 0.5rem;
  font-size: 1.1rem;
  letter-spacing: 1px;
}

/* MAIN FOOTER */

#main-footer {
  /* position: absolute;
  bottom: 0;
  left: 0; */
  padding: 2rem;
  background-color: #000;
  color: #EEEEEE;
  color: var(--lightgray);
}
