  margin-bottom: 1rem;
  font-size: 1.2rem;
  color: #32A1D5;
  color: var(--adullam-blue);
}
#main-footer p {
  font-size: 1rem;
}
.footer-container li {
  list-style: none;
  margin-top: 1rem;
}
.footer-container a {
  text-decoration: none;
  color: #ccc;
}
.footer-container a:hover {
  color: #32A1D5;
  color: var(--adullam-blue);
}
.footer-container {
  margin: 0 auto;
}
.social-heading {
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  color: #32A1D5;
  color: var(--adullam-blue);
}
.sitemap,
.special,
.contact-us,
.contact-form {
  padding: 1rem;
  margin-right: 1rem;
}
.contact-us i {
  font-size: 1.1rem;
}
.contact-us i:hover {
  color: #32A1D5;
  color: var(--adullam-blue);
}
.contact-us .social-icon {
  font-size: 1.5rem;
  margin-right: 1rem;
}
.contact-form {
  display: flex;
  flex-direction: column;
}
.footer-logo {
  width:9rem;
  height: auto;
  margin-top: 1.5rem;
}
.contact-form {
  width: 300px;
  padding: 1.1rem;
  border-radius: 0.6rem;
  background-color: whitesmoke;
}
.form-control {
  margin-top: 0.8rem;
  padding: 0.8rem;
  border: none;
  outline: none;
  border-bottom: 1px solid #32A1D5;
  border-bottom: 1px solid var(--adullam-blue);
}
.contact-form .submit-btn {
  width: 25%;
  background: #32A1D5;
  background: var(--adullam-blue);
  color: #fff;
  font-size: 0.9rem;
  font-weight: bold;
  cursor: pointer;
  padding: 0.5rem;
}
.footer-items {
  display: flex;
  align-items: center;
}
.copyright-text {
  text-align: center;
  margin-top: 1rem;
}

