.register {
    padding: 3.5rem ;
    width: 75vw;
    margin: 6rem auto;
    box-shadow: 0 4px 10px rgba(0,0,0,.3);
    border-radius: 5px;
}
.reg__logo {
    width: 6.25rem;
    display: block;
    margin: 0 auto;
    margin-bottom: 0.5rem;
}

.register__heading {
    font-size: 1.5rem;
    margin: 1.5rem auto;
    text-align: center;
}
.required {
    color: red;
    font-size: 1rem;
}
.register__header a:link, 
.register__header a:visited {
    text-decoration: none;
    color: var(--adullam-blue);
    font-size: 1.1rem;
}
.register__header p,
.register__header a:link,
.register__header a:visited  {
    font-size: .9rem;
}
.register__header span {
    font-size: 1.6rem;
}
.register__content {
    padding-top: 2.5rem;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, minmax(-webkit-min-content, 18rem));
    grid-template-columns: repeat(auto-fit, minmax(min-content, 18rem));
    grid-column-gap: 1rem;
    grid-row-gap: 1.5rem;
}
.login__content {
    padding-top: 2.5rem;
}
.reg__label {
    background-color: var(--adullam-blue);
    color: #fff;
    font-size: 1.05rem;
    width: 40%;
    padding: .6rem;
    margin-top: 1rem;
    border-radius: 3px;
}
.register__content .note {
    grid-column: 1/-1;
    line-height: 1.7;
    text-align: justify;
}
.form__control {
    margin-top: 1.3rem;
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
}
.register__content .form__control--input, 
.register__content .form__control--input:focus,
.login__content .form__control--input,
.login__content .form__control--input:focus {
    width: 100%;
    padding: .7rem 1rem;
    font-size: 1.05rem;
    border: none;
    outline: none;
    border-bottom: 1.03px solid var(--adullam-blue);
    background-color: rgba(0,0,0,.02);
    border-top-right-radius: 5px;
}

.login__content .form__control--input,
.login__content .form__control--input:focus {
    width: 50%;
    margin-top: 2rem;
    font-size: 1.2rem;
} 

.register__content label {
    display: block;
    font-size: 1rem;
    margin: 0.7rem 0 0.3rem 0;
}
.next__prev--box {
    display: flex;
    justify-content: space-between;
    margin: 2rem auto;
    padding: 1rem;
    width: 100%;
    grid-column: 1/-1;
}
.formNavigator {
    display: block;
    border: none;
    cursor: pointer;
    background-color: var(--adullam-blue);
    color: #fff;
    font-size: 1.1rem;
    padding: .8rem;
    border-radius: 100px;
}
.submit {
    display: block;
    border: none;
    background-color: var(--adullam-blue);
    color: #fff;
    font-size: 1.1rem;
    padding: .8rem;
    border-radius: 100px;
    cursor: pointer;
    transition: all .2s;
}
.submit:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    box-shadow: 0 3px 5px rgba(0,0,0,.3);
}
@media screen and (max-width: 56.25em) {
    .register {
        padding: 2rem ;
        width: 85vw;
        margin: 6rem auto;
    }
    .register__content {
        padding-top: 1rem;
        width: 100%;
    }
    .reg__label {
        width: 60%;
        padding: .6rem;
    }
}
@media screen and (max-width: 48em) {
    .register {
        padding: 2rem ;
        width: 85vw;
        margin: 6rem auto;
        font-size: 0.8rem;
    }
    .register__heading {
        font-size: 1.5rem;
        font-size: 1.2rem;
        margin-bottom: 1.5rem;
        text-align: center;
    }
    .register__content {
        padding-top: 1rem;
        grid-template-columns: repeat(auto-fit, minmax(-webkit-min-content, 80%));
        grid-template-columns: repeat(auto-fit, minmax(min-content, 80%));
        width: 100%;
    }
    .reg__label {
        width: 100%;
        padding: .6rem;
    }
}

/* COLOURS */
/* 
ADULLAM BLUE            #32A1D5,
FOOTER & ACTIVE BLACK   #1E1E1E
GRAY                    #C4C4C4
LIGHT GRAY              #EEEEEE
*/

:root {
    /* color variables */
    --adullam-blue: #32A1D5;
    --footer-black: #1E1E1E;
    --gray-colour: #c4c4c4;
    --lightgray: #EEEEEE;
}
/* display header */

.about__header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50vh;
    background-image: linear-gradient(rgba(0,0,0, 0.6), rgba(0,0,0, 0.6)), url(/static/media/slider-image.aaf8a438.jpg);
    /* background-attachment: fixed; */
    background-repeat: no-repeat;
    background-size: cover;
}

/* about display header */
.about__header header h1 {
    color: #fff;
}
.more__info--content {
    display: flex;
}
.info__img--container {
    margin: 1rem;
}
.info__img--container img {
    width: 100%;
    height:
    auto;
}
.more__info--text {
    text-align: justify;
}
/* semester section */
#semesters {
    background: #EEEEEE;
    background: var(--lightgray);
    padding: 2rem 0;
}
.semesters__details {
    display: flex;
    flex-direction: column;
}
.semesters-card {
    display: flex;
    justify-content: center;
    align-items: center;
}
.semester-card {
    text-align: center;
    margin: 1rem;
    padding: 1rem;
    background-color: #ccc;
    border-radius: 0.8rem;
    max-width: 23rem;
    min-height: 28rem;
}
.semester-card p {
    line-height: 1.7;
    text-align: justify;
}
.semester-card .readmore-btn {
    align-self: flex-end;
}

/* program options */

.program__options--container {
    display: flex;
    justify-content: center;
}
.program__option {
    margin: 1rem;
    text-align: center;
    max-width: 30rem;
}
.program__option--img {
    width: 100%;
    height: auto;
    margin: 1rem auto;
}
.program__option--img img {
    width: 100%;
    height: auto;
}
.program__option--text p {
    text-align: justify;
    line-height: 1.5;
    margin-bottom: 2rem;
}

/* faculties */
.faculty-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
}
.faculty-card {
    margin: 1rem;
}
.faculty-card img {
width: 12.5rem;
height: 12.2rem;
border-radius: 50%;
margin-bottom: 1rem;
}
.faculty-card p {
    font-size: 1.1rem;
    font-weight: 600;
    color: #32A1D5;
    color: var(--adullam-blue);
}

@media all and (max-width: 48rem){
    .about__header {
        height: 25vh;
    }
    .about__header h1 {
        font-size: 2.7rem;
    }
}
@media screen and (max-width: 42.5rem){
    .about__header h1 {
        font-size: 2rem;
    }
    .more__info--content {
        flex-wrap: wrap;
        padding: 1.5rem;
    }
    .semesters__details  p {
        padding: 0 2rem;
    }
    .semesters-card {
        /* padding: 0 4rem; */
        flex-wrap: wrap;
    }
    .program__options--container {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 33.75rem) {

        /* Hide main navigation and show mobile menu */
        .main-header {
            display: none;
        }
        .mobile-menu {
            display: block;
        }
        .menu-bar {
            background: #32A1D5;
            background: var(--adullam-blue);
        }
    .about__header {
        height: 20vh;
    }
    .about__header h1 {
        font-size: 1.1rem;
    }
    .semester-text p {
        text-align: center;
        padding: 0.2rem;
    }

}
